export default "@font-face {\n  font-family: 'Source Sans Pro';\n  src: url('SourceSansPro-Regular.ttf');\n}\n\n/* main colors */\n\n/* main block on page */\n\n/* header block styles */\n\n.header {\n  background-color: #7B1FA2;\n  color: #fff;\n  font-size: 20px;\n  display: flex;\n}\n\n.header__logo span {\n  margin-right: 8px;\n}\n\n.header__actions {\n  margin-left: auto;\n  display: flex;\n}\n\n.header__actions > div {\n  margin-right: 12px;\n}\n\n.header__actions > div:last-child {\n  margin-right: 0;\n}\n\n.header__actions-search form label {\n  display: flex;\n  align-items: center;\n}\n\n.header__actions-search form label:hover input {\n  width: 200px;\n  opacity: 1;\n}\n\n.header__actions-search form input[type='search'] {\n  width: 0;\n  opacity: 0;\n  transition: width 0.4s cubic-bezier(0, 0.795, 0, 1);\n}\n\n.header__actions-login {\n  position: relative;\n}\n\n.header__actions-login:hover {\n  cursor: pointer;\n}\n\n.header__actions-login:hover .tooltip {\n  display: block;\n}\n\n.main {\n  flex: 1 1 0;\n  margin-bottom: 80px;\n}\n\n.footer {\n  background-color: #7B1FA2;\n  color: #fff;\n  display: flex;\n}\n\n.footer__name {\n  margin-left: auto;\n}\n\n/* items in footer & header box */\n\n.box_items {\n  display: flex;\n  padding-bottom: 16px;\n  padding-top: 16px;\n  min-width: 950px;\n}\n\n.tooltip {\n  position: absolute;\n  width: 200px;\n  display: none;\n  top: 30px;\n  min-height: 40px;\n  left: -80px;\n  transition: all 0.5s ease;\n  background-color: #fff;\n  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.75);\n  padding: 10px 15px;\n  color: #000;\n}\n\n.tooltip h5 {\n  font-size: 18px;\n  margin: 0;\n}\n\n.tooltip:before, .tooltip:after {\n  content: \"\";\n  position: absolute;\n  left: 80px;\n  border-left: 10px solid transparent;\n  border-right: 10px solid transparent;\n  border-top: 10px solid transparent;\n}\n\n.tooltip:before {\n  top: -21px;\n  border-bottom: 10px solid rgba(168, 168, 168, 0.5);\n}\n\n.tooltip:after {\n  top: -20px;