export default "@font-face {\n  font-family: 'Source Sans Pro';\n  src: url('SourceSansPro-Regular.ttf');\n}\n\n/* main colors */\n\n.switcher {\n  display: flex;\n  align-items: center;\n  font-size: 14px;\n  padding-bottom: 3px;\n}\n\n.switcher label {\n  margin-left: auto;\n}\n\n.switcher__box {\n  position: relative;\n  display: inline-block;\n  width: 40px;\n  height: 20px;\n}\n\n.switcher__box input {\n  display: none;\n}\n\n.switcher__box input:checked + .slider {\n  background-color: #7B1FA2;\n}\n\n.switcher__box input:checked + .slider:before {\n  transform: translateX(18px);\n}\n\n.switcher__box .slider {\n  position: absolute;\n  cursor: pointer;\n  top: 0;\n  left: 0;\n  right: 0;\n  bottom: 0;\n  background-color: #BDBDBD;\n  transition: .4s;\n  border-radius: 20px;\n}\n\n.switcher__box .slider:before {\n  position: absolute;\n  content: \"\";\n  height: 15px;\n  width: 15px;\n  left: 4px;\n  top: 2px;\n  background-color: white;\n  transition: .4s;\n  border-radius: 50%;\n}\n\n/*# sourceMappingURL=data:application/json;base64, */"